<template>
    <div class="startpage container-fluid">



        <div class="container starttext fade-in">


            <div v-if="started" class="judging text-center">
                <Matching
                    :potentialMatches="usersData"
                    :arrayLength="arrayLength"
                    :noUsers="noUsers"
                    @clicked="emptyArray"
                /> 
            </div>
            <div v-else class="">
                <div class="left">
                    <h1>Enter the Queue</h1>
                    <div class="typingtext">
                        <vue-typed-js :strings="['artists^2000', 'friends^2000', 'musicians^2000']" :loop="true" :showCursor="false" :typeSpeed="50">
                            <h2>Meet <span class="typing"></span></h2>
                        </vue-typed-js>
                    </div>
                    <div class="" @click="getPotentialMatches()">
                        <div class="buttonfade">
                            <ParticleBtn 
                                ref="start"
                                class="startbtn"
                                :visible.sync="btnOps.visible"
                                :animating.sync="btnOps.animating"
                                :options="btnOps"
                                cls="btn-cls">
                                Enter
                            </ParticleBtn>
                        </div>
                    </div>
                </div>

            </div>
            <!-- <div v-else class="startbtn">
                <a class="" ref="start" @click="getPotentialMatches(), pop()">Start</a>
            </div>   -->
        </div>
        <svg class="wave" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
            <path fill="#fff" fill-opacity="1" d="M0,128L48,144C96,160,192,192,288,176C384,160,480,96,576,85.3C672,75,768,117,864,144C960,171,1056,181,1152,181.3C1248,181,1344,171,1392,165.3L1440,160L1440,0L1392,0C1344,0,1248,0,1152,0C1056,0,960,0,864,0C768,0,672,0,576,0C480,0,384,0,288,0C192,0,96,0,48,0L0,0Z"></path>
        </svg>

    </div>
</template>

<script>
import * as firebase from 'firebase/app';
import'firebase/database';
import 'firebase/firestore';
import {db} from '../../main';

import Matching from './Matching';

import ParticleBtn from "vue-particle-effect-buttons";

    export default {
        components: {
            Matching,
            ParticleBtn,
        },
        data() {
            return {
                userList: [],
                usersData: [],
                started: false,
                arrayLength: 0,
                noUsers: false,

                btnOps: {
                    type: "circle",
                    style: 'fill',
                    easing: "easeOutQuart",
                    size: 4,
                    direction: 'bottom',
                    particlesAmountCoefficient: 2,
                    oscillationCoefficient: 5,
                    color: function () {
                    return Math.random() < 0.5 ? "#eb9788" : "#ffffff";
                    },
                    onComplete: () => {  
                    },
                    onBegin: () => {
                    },
                    visible: true,
                    animating: false
                },
            }
        },
        mounted () {

        },
        created () {
            // check for online users
            this.loading = true;
            // Fetch the current user's ID from Firebase Authentication.
            const uid = firebase.auth().currentUser.uid;

            // Create a reference to this user's specific status node.
            // This is where we will store data about being online/offline.
            const userStatusDatabaseRef = firebase.database().ref('/status/' + uid);

            // We'll create two constants which we will write to 
            // the Realtime database when this device is offline
            // or online.
            const isOfflineForDatabase = {
                state: 'offline',
                last_changed: firebase.database.ServerValue.TIMESTAMP,
            };

            const isOnlineForDatabase = {
                state: 'online',
                last_changed: firebase.database.ServerValue.TIMESTAMP,
            };

            const setData = {
                id: uid,
            }

            userStatusDatabaseRef.update(setData);

            firebase.database().ref('status').once('value', snapshot => {
                snapshot.forEach(el => {
                    if (el.val().id == undefined) {
                        firebase.database().ref('status/' + el.key).remove();
                    }
                })
            });
            // Create a reference to the special '.info/connected' path in 
            // Realtime Database. This path returns `true` when connected
            // and `false` when disconnected.
            firebase.database().ref('.info/connected').on('value', function(snapshot) {
                // If we're not currently connected, don't do anything.
                if (snapshot.val() == false) {
                    return;
                }

                // If we are currently connected, then use the 'onDisconnect()' 
                // method to add a set which will only trigger once this 
                // client has disconnected by closing the app, 
                // losing internet, or any other means.
                userStatusDatabaseRef.onDisconnect().update(isOfflineForDatabase).then(function() {
                    // The promise returned from .onDisconnect().set() will
                    // resolve as soon as the server acknowledges the onDisconnect() 

                    // We can now safely set ourselves as 'online' knowing that the
                    // server will mark us as offline once we lose connection.
                    userStatusDatabaseRef.update(isOnlineForDatabase);
                });
            });
        },
        methods: {
            // Animation
            

            // Technical stuff
            getPotentialMatches() {
                const uid = firebase.auth().currentUser.uid;
                // get users from database based on when they last logged in
                firebase.database().ref('status').limitToLast(50).once('value', snapshot => {
// .orderByChild('last_changed')
                    // initialize arrays
                    const metUsers = [];
                    this.noUsers = false;

                        console.log('looking for potential matches');

                        // fill array with id's of users you've already met from firestore
                        db.collection('matches')
                        .doc(uid)
                        .collection('usersMet')
                        .get()
                        .then( snap => {
                            snap.forEach(el => {
                                metUsers.push(el.id);
                            });

                            console.log('filling list');

                            // fill array with users you've not met based on the previous array
                            snapshot.forEach(el => {
                            if (metUsers.includes(el.val().id) || el.val().id == uid) {
                                console.log('user met');
                            }
                            else {
                                if (this.userList.length != 10) {
                                    this.userList.push(el.val().id);
                                }
                                else {
                                    return this.userList;
                                }
                            }
                        });
                        console.log('getting users data');

                        // fill a new array with data objects from the selected users
                        this.userList.forEach(doc => {
                            db.collection('users')
                            .doc(doc)
                            .get()
                            .then(el => {
                                const document = el.data();
                                this.usersData.push({
                                    id: document.userId,
                                    username: document.username,
                                    bio: document.bio,
                                    audio: document.audio
                                })
                            })
                        });
                        // set variable to length of the array
                        this.arrayLength = this.userList.length;

                        if (this.arrayLength == 0) {
                            this.noUsers = true;
                        }
                        
                        console.log('data pending');
                    })
                })
                // start the matching process
                setTimeout(() => this.started = true, 2000);
            },
            emptyArray() {
                // this.started = false;
                // empty the arrays
                this.userList = [];
                this.usersData = [];
                
                // when the arrays are empty refire the getPotentialMatches() function
                if (this.userList.length == 0  && this.usersData.length == 0) {
                    this.getPotentialMatches();
                }
            }
        },
    }
</script>